<template>
    <div class="">
        <div class="issue-header">评论</div>
        <div class="issue-form">
            <div class="issue-title"><input class="issue-input" v-model="newIssue.title" placeholder="输入问题..." /></div>
            <div class="issue-body"><EditableDiv class="issue-input" v-model="newIssue.body"></EditableDiv></div>
            <div class="issue-action"> <button class="submit-btn" :disabled="processing === 'createIssue' || !newIssue.title || !newIssue.body" v-on:click="createIssue()">评论</button></div>

        </div>
        <div class="issue-list">
            <div class="issue-item" v-for="(item, index) in issues" >
                <div class="issue-avatar" v-bind:style="{ backgroundImage: 'url(' + item.node.author.avatarUrl + '&s=42)' }">

                </div>
                <div class="issue-box">
                    <template v-if="updatedIssue.id === item.node.id">
                        <div class="issue-box-title"><input class="issue-input" v-model="updatedIssue.title" placeholder="输入问题..." /></div>
                        <div class="issue-box-body" ><EditableDiv class="issue-input" v-model="updatedIssue.body"></EditableDiv></div>
                        <div class="issue-box-footer">
                            <span class="pull-right">
                                <button class="issue-reply-button submit-btn" :disabled="processing==='modifyIssue' || !updatedIssue.title || !updatedIssue.body" v-on:click="modifyIssue()">提交</button>
                                <button class="issue-reply-button submit-btn" :disabled="processing==='modifyIssue'" v-on:click="toModifyIssue(null)">取消</button>
                            </span>
                        </div>
                    </template>
                    <template v-else>
                        <div class="issue-box-title"><span class="issue-username">{{item.node.author.login}} </span>{{item.node.title}}</div>
                        <div class="issue-box-body" >{{item.node.body}}</div>
                        <div class="issue-box-footer">
                            {{new Date(item.node.createdAt).toLocaleString()}}
                            <span class="pull-right">
                                <a class="issue-button" v-on:click="replyIssueId = item.node.id">回复({{item.node.comments.totalCount}})</a>
                                <a class="issue-button" v-if="user.admin" v-on:click="toModifyIssue(item.node)">修改</a>
                                <a class="issue-button" v-if="user.admin" v-on:click="removeIssue(item.node.id)">删除</a>
                            </span>
                        </div>
                    </template>

                    <div class="issue-comment-list">
                        <div class="issue-box-reply" v-if="replyIssueId === item.node.id">
                            <input class="issue-reply-input issue-input"  autofocus v-model="newComment.body" :placeholder="'回复' + item.node.author.login + '...'" />
                            <button class="submit-btn issue-reply-button" :disabled="processing === 'createComment' || !newComment.body" v-on:click="createComment(index, item.node.id)">回复</button>
                        </div>

                        <div class="issue-comment-item" v-for="comment in item.node.comments.nodes">
                            <div class="issue-avatar" v-bind:style="{ backgroundImage: 'url(' + comment.author.avatarUrl + '&s=42)' }">
                            </div>
                            <div class="issue-box">
                                <template v-if="updatedComment.id === comment.id">
                                    <div class="issue-box-body"><input class="issue-input" v-model="updatedComment.body" placeholder="输入回复..." /></div>
                                    <div class="issue-box-footer">
                                         <span class="pull-right">
                                            <button class="issue-reply-button submit-btn" :disabled="processing==='modifyComment' || !updatedComment.body" v-on:click="modifyComment(index, item.node.id, comment.id)">提交</button>
                                            <button class="issue-reply-button submit-btn" :disabled="processing==='modifyComment'" v-on:click="toModifyComment(null)">取消</button>
                                         </span>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="issue-box-title"><span class="issue-username">{{comment.author.login}} </span></div>
                                    <div class="issue-box-body" >{{comment.body}}</div>

                                    <div class="issue-box-footer">
                                        {{new Date(comment.createdAt).toLocaleString()}}
                                        <span class="pull-right">
                                            <a class="issue-button" v-if="user.admin" v-on:click="toModifyComment(comment)">修改</a>
                                            <a class="issue-button" v-if="user.admin" v-on:click="removeComment(index, item.node.id, comment.id)">删除</a>
                                        </span>
                                    </div>
                                </template>
                            </div>
                        </div>
                    </div>
                    <div class="fetch-more-issue-comment" v-if="item.node.comments.pageInfo.hasNextPage" v-on:click="loadMoreComment(index, item.node.id, item.node.comments.pageInfo.endCursor)">
                        {{processing === 'listComment' + item.node.id && '加载中...' || '查看更多'}}
                    </div>
                </div>
            </div>

            <div class="fetch-more-issue" v-if="pageInfo.hasNextPage" v-on:click="loadMoreIssue(pageInfo.endCursor)">  {{processing === 'listIssue' && '加载中...' || '查看更多'}}</div>

        </div>

    </div>
</template>

<script>

    import {api} from '../github';

    import {mapMutations, mapState} from 'vuex'
    import EditableDiv from "../components/EditableDiv";

    export default {
        name: 'IssuePanel',
        components: {EditableDiv},
        props: {
            "path": String,
            "repo": String
        },
        data() {
            return {
                processing: false,
                issues: [],
                pageInfo: {},
                totalCount: null,
                replyIssueId: null,
                newIssue: {
                    title: null,
                    body: null
                },
                updatedIssue: {
                    id: null,
                    title: null,
                    body: null
                },
                newComment: {
                    body: null
                },
                updatedComment: {
                    id: null,
                    body: null
                }

            }
        },
        computed: {
            ...mapState([
                'dir',
                'file',
                'user'
            ])
        },
        created() {
        },
        beforeDestroy() {
        },
        mounted() {
            this.refresh();
        },
        watch: {
            path(newValue, prevValue){
                this.refresh();
            },
            repo(newValue, prevValue){
                this.refresh();
            }
        },
        methods: {
            ...mapMutations([
                'receiveFile',
                'receiveDir'
            ]),
            requiredLogin(){
                if(!this.user.login){
                    this.$fail("请先登录github账号!");
                    throw Error("login required");
                }

            },
            refresh(){
                this.newIssue = {
                    title: null,
                    body: null
                };
                this.updatedIssue = {
                    id: null,
                    title: null,
                    body: null
                };
                this.newComment = {
                    body: null
                };
                this.updatedComment = {
                    id: null,
                    body: null
                };
                api.repo(this.repo).issues(this.path).then(issues =>{
                    this.issues = issues.repository.issues.edges;
                    this.pageInfo = issues.repository.issues.pageInfo;
                    this.totalCount = issues.repository.issues.totalCount;
                });
            },
            loadMoreIssue(cursor){
                this.processing = 'listIssue';
                api.repo(this.repo).issues(this.path, cursor).then(issues =>{
                    this.issues = this.issues.concat(issues.repository.issues.edges);
                    this.pageInfo = issues.repository.issues.pageInfo;
                    this.totalCount = issues.repository.issues.totalCount;
                }).catch(err =>{
                    this.$fail(err.message);
                }).finally(res => {
                    this.processing = false;
                });
            },
            createIssue(){
                this.requiredLogin();
                this.processing = 'createIssue';
                api.repo(this.repo).createIssue(this.path, this.newIssue.title, this.newIssue.body).then(issues =>{
                    this.$success('评论成功');
                    this.refresh();
                }).catch(err =>{
                    this.$fail(err.message);
                }).finally(() =>{
                    this.processing = false;
                });
            },
            toModifyIssue(node){
                if(node){
                    this.updatedIssue.id = node.id;
                    this.updatedIssue.title = node.title;
                    this.updatedIssue.body = node.body;
                }else{
                    this.updatedIssue = {
                        id: null,
                        title: null,
                        body: null
                    };
                }

            },
            modifyIssue(){
                this.processing = 'modifyIssue';
                api.repo(this.repo).updateIssue(this.updatedIssue.id, this.updatedIssue.title, this.updatedIssue.body).then(issues =>{
                    this.$success('评论修改成功');
                    this.refresh();
                }).catch(err =>{
                    this.$fail(err.message);
                }).finally(() =>{
                    this.processing = false;
                });
            },
            removeIssue(nodeId){
                this.$confirm('此操作将永久删除评论, 是否继续?', () => {
                    this.processing = 'deleteIssue';
                    return api.repo(this.repo).deleteIssue(nodeId).then(result => {
                        this.$success('删除成功!');
                        this.refresh();
                    }).catch(err =>{
                        this.$fail(err.message);
                    }).finally(() =>{
                        this.processing = false;
                    });

                });
            },
            loadMoreComment(index, issueId, cursor){
                this.processing = 'listComment' + issueId;
                api.repo(this.repo).issueComments(issueId, cursor).then(result =>{
                    let comments = this.issues[index].node.comments.nodes;
                    if(cursor && comments){
                        this.$set(this.issues[index].node.comments, 'nodes',  comments.concat(result.node.comments.nodes))
                    }else{
                        this.$set(this.issues[index].node.comments, 'nodes',  result.node.comments.nodes)
                    }

                    this.$set(this.issues[index].node.comments, 'totalCount',  result.node.comments.totalCount)
                    this.$set(this.issues[index].node.comments, 'pageInfo',  result.node.comments.pageInfo)
                }).catch(err =>{
                    this.$fail(err.message);
                }).finally(() =>{
                    this.processing = false;
                });
            },
            createComment(index, issueId){
                this.requiredLogin();
                this.processing = "createComment";
                api.repo(this.repo).createIssueComment(issueId, this.newComment.body).then(result =>{
                    this.$success('回复成功');
                    this.newComment.body = null;
                    this.loadMoreComment(index, issueId);
                }).catch(err =>{
                    this.$fail(err.message);
                }).finally(() =>{
                    this.processing = false;
                });
            },
            toModifyComment(comment){
                if(comment){
                    this.updatedComment.id = comment.id;
                    this.updatedComment.body = comment.body;
                }else{
                    this.updatedComment = {
                        id: null,
                        body: null
                    };
                }
            },
            modifyComment(index, issueId, commentId){
                this.processing = "modifyComment";
                api.repo(this.repo).updateIssueComment(commentId, this.updatedComment.body).then(result =>{
                    this.$success('修改成功');
                    this.updatedComment.id = null;
                    this.updatedComment.body = null;
                    this.loadMoreComment(index, issueId);
                }).catch(err =>{
                    this.$fail(err.message);
                }).finally(() =>{
                    this.processing = false;
                });
            },
            removeComment(index, issueId, commentId){
                this.$confirm('此操作将永久删除回复, 是否继续?', () => {
                    this.processing = 'removeComment';
                    return api.repo(this.repo).deleteIssueComment(commentId).then(result => {
                        this.$success('删除成功!');
                        this.loadMoreComment(index, issueId);
                    }).catch(err =>{
                        this.$fail(err.message);
                    }).finally(() =>{
                        this.processing = false;
                    });

                });
            }
        }
    }

</script>



<style scoped>

    .issue-header{
        color: #8a9aa9;
        font-weight: 400;
        text-align: center;
        padding: 1.67rem 0 5px;
    }

    .issue-form {
        display: flex;
        flex-direction: column;
        position: relative;
        padding: 1rem 1.333rem;
        background-color: #fafbfc;
        border-radius: 3px;
    }

    .issue-title, .issue-body, .issue-action{
        flex: 1;
        padding-top: 1rem ;
    }

    .issue-input {
        padding: .75rem;
        width: 100%;
        border: 1px solid #e9e9e9;
        border-radius: 2px;
        outline: none;
        box-sizing: border-box;
        resize: none;
        font-size: 1rem;
        font-weight: 400;
        background-color: #fff;
    }

    .issue-body .issue-input{
        min-height: 5rem;
    }

    .issue-action {
        display: flex;
        align-items: center;
        margin: .65rem 0 0;
    }

    .submit-btn {
        flex: 0 0 auto;
        margin-left: auto;
        -webkit-appearance: none;
        appearance: none;
        background-color: #007fff;
        color: #fff;
        border-radius: 2px;
        border: none;
        padding: .5rem 1.3rem;
        outline: none;
        transition: background-color .3s,color .3s;
        cursor: pointer;
    }

    .submit-btn:hover{
        background-color: #0371df;
    }

    .submit-btn:disabled {
        cursor: default;
        opacity: .4;
    }

    .issue-item {
        display: flex;
    }

    .issue-avatar {
        margin-top: 1.5rem;
        margin-left: .5rem;
        width: 2.5rem;
        height: 2.5rem;
        border-radius: 50%;
    }

    .issue-box{
        margin-top: 1rem;
        margin-left: 1rem;
        flex: 1 1 auto;
    }

    .issue-item:not(:last-child) > .issue-box{
        border-bottom: 1px solid #f1f1f1;
    }

    .issue-comment-item:not(:last-child) > .issue-box{
        border-bottom: 1px solid #f1f1f1;
    }

    .issue-box-title, .issue-box-body , .issue-box-footer{
        color: #505050;
        margin-bottom: 1rem;
        display: flex;
    }

    .issue-box-title {
        font-weight: 600;
    }

    .issue-box-footer{
        color: #8a9aa9;
    }

    .issue-box-footer .pull-right{
        margin-left: auto;
    }

    .issue-box-footer .issue-button {
        text-decoration: none;
        color: #8a9aa9;
        margin-right: .5rem;
        cursor: pointer;
    }

    .issue-box-title .issue-username {
        color: #8a9aa9;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        max-width: 16rem;
        margin-right: 1rem;
    }

    .issue-comment-item {
        display: flex;
        background-color: #fafbfc;
    }

    .issue-comment-list {
        margin-top: 1rem;
    }

    .issue-box-reply {
        display: flex;
        align-items: center;
        background-color: #fafbfc;
        padding: 1rem 1rem 1rem 1rem;
    }

    .issue-reply-button {
        margin-left: 1rem;
    }

    .fetch-more-issue, .fetch-more-issue-comment {
        padding-top: 1rem;
        padding-bottom: 1rem;
        text-align: center;
        color: #406599;
        cursor: pointer;
        user-select: none;
    }


</style>
