<template>
</template>

<script>
    import { api } from '../github';
    export default {
        name: "Login",
        mounted() {

            if(this.$route.query.me){
                let oauthApps = "client_id=" + api.preset("oauthAppsClientId");
                window.location = "https://github.com/login/oauth/authorize?" + oauthApps + "&redirect_uri=" + encodeURIComponent(this.authorizeUrl(oauthApps)) + "&scope=repo&login=" + api.preset("owner") + "&state=" + Math.floor(Math.random()*100000);
            }
            else{
                let githubApps = "client_id=" + api.preset("githubAppsClientId");
                window.location = "https://github.com/login/oauth/authorize?" + githubApps + "&redirect_uri=" + encodeURIComponent(this.authorizeUrl(githubApps)) + "&state=" + Math.floor(Math.random()*100000);
            }
        },
        methods: {
            authorizeUrl(apps){
                return api.preset("authorizeUrl") + "?" + apps + "&redirect_uri=" + encodeURIComponent(this.$route.query.redirectUrl || "");
            }
        }
    }
</script>

