<template>
    <div class="container fluid-container">
        
       
        <div class="entry-list" v-bind:class="(showDir ? 'entry-fixed' : 'entry-hidden')" v-on:click="receiveToggleDir()" @touchmove.prevent="(function(){})()" >
            <DirectoryPanel v-bind:loading="loading" v-bind:items="items" v-bind:path="path" v-bind:repo="repo"></DirectoryPanel>
            <div v-if="showDir" class="mask-bottom"></div>
        </div>

        <div class="entry-body">
            <template v-if="!preview">
                <span>{{loading && "加载中..." || "markdown预览"}}</span>
            </template>
            <template v-else>
                <ArticleBodyPanel v-bind:preview="preview" v-bind:repo="repo" v-bind:path="path"></ArticleBodyPanel>
            </template>
        </div>

    </div>
</template>

<script>

    import { api } from '../github';
    import DirectoryPanel from "./DirectoryPanel";
    import { mapMutations, mapState } from 'vuex'
    import ArticleBodyPanel from "./ArticleBodyPanel";

    export default {
        name: 'ArticlePanel',
        components: {
            ArticleBodyPanel,
            DirectoryPanel
        },
        props: {
            "path": String,
            "repo": String
        },
        data() {
            return {
                loading: false,
                items: null,
                preview: null,
                state: null
            }
        },
        computed: {
            ...mapState([
                'dir',
                'file',
                'repoKey',
                'showDir',
            ])
        },
        beforeDestroy() {
        },
        mounted() {
            this.refresh(this.path).then(() => {
                 this.restoreHash();
            })
        },
        activated(){
            if(this.state){
                this.receiveFile(this.state.file);
                this.receiveDir(this.state.dir);
            }
            
            if(this.$route.query.dirty){
                this.refresh(this.path, true);
            }
        },
        deactivated(){

            this.state = {};
            this.state.file = this.file;
            this.state.dir = this.dir;
            this.receiveHistory([this.repo, this.path])
        },
        watch: {
            path(newValue, prevValue){
                this.refresh(newValue);
            },
            "$route.hash"(){
                this.restoreHash();
            }
        },
        methods: {
            ...mapMutations([
                'receiveFile',
                'receiveDir',
                'receiveHistory',
                'receiveToggleDir'
            ]),
            refresh(path, updateDir){
                return this.showContent(path || '', updateDir)
            },
            restoreHash(){
                if(!this.$route.hash){
                    return ;
                }

                let anchor = document.querySelector("[href='" + decodeURIComponent(this.$route.hash) + "']");
                if(anchor){
                    let elementPosition = anchor.offsetTop;
                    let offsetPosition = elementPosition - 16;

                    window.scrollTo({
                        top: offsetPosition,
                        behavior: "smooth"
                    });
                }
            },
            markedItem(path){
                this.receiveFile(this.items.find(it =>{
                    return it.path === path;
                }));
            },
            showContent(path, updateDir) {
                let dir = null;
                this.loading = true;
                return api.repo(this.repo).contents(path).then((promise) => {
                    if(promise.type === 'file'){
                        this.preview = promise.data;
                        dir = api.parentDir(path);
                        //目录树需要更新
                        if(updateDir || dir !== this.dir || this.items === null) {
                            return this.showContent(dir).then((dirPromise) => {
                                this.markedItem(path);
                                return dirPromise;
                            })
                        }
                        else {
                            this.markedItem(path);
                        }
                    }
                    else {
                        this.items = promise.data;
                        dir = path;
                    }
                    this.receiveDir(dir);
                    return promise;
                }).catch(reason => {
                    this.preview = reason.message;
                    if(updateDir){
                        return this.showContent(api.parentDir(path));
                    }
                }).finally(() => {
                    this.loading = false;
                });
            }
        }
    }

</script>



<style scoped>
    @import '~github-markdown-css/github-markdown.css';


    .entry-list {
        position: sticky;
        top: 5rem;
        margin-top: 1rem;
    }

    .entry-list {
        display: block !important;
        flex: 1;
        box-sizing: border-box;
        box-shadow: 0 2px 10px rgba(0,0,0,.08);
    }

    .entry-body {
        flex: 4;
        box-sizing: border-box;
        box-shadow: 0 2px 10px rgba(0,0,0,.08);
        margin: 1rem 0 2rem 1rem;
        padding: 1rem 1rem 1rem 2rem;
        overflow: hidden;
    }
    

   @media all and (max-device-width: 800px){
        
        .entry-body {
            margin-bottom: 5rem;
            margin-top: 0;
            margin-left: 0;
            margin-right: 0;
            box-shadow: none;
        }
        
        
        .entry-list {
            position: fixed;
            z-index: 999;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            opacity: 1;
            margin-top: 0;
            padding-top: 4rem;
            
        }
        
        .entry-fixed {
            display: block !important;
        }
        
         .entry-hidden {
            display: none !important;
        }
        
        .mask-bottom {
            height: 100%;
            background: rgba(0, 0, 0, 0.6);
        }
      
    }

</style>
