<template>
    <div class="container fluid-container">
        <div class="search-panel markdown-body" >
            <template v-for="item in searchItems">
                <div class="search-header">
                    <pre><a href="javascript:void(0);" v-on:click="showFile(item.path)" >{{item.name}}</a></pre>
                    <template v-for="matchText in item['text_matches']">
                        <pre class="matchText" v-html="pretty(matchText.fragment, matchText.matches)"></pre>
                    </template>
                </div>
            </template>
            <div class="search-header" v-if="!loading && searchItems.length === 0">
                未找到任何结果
            </div>

            <div class="search-footer" v-if="incompleteResults">
                <pre v-if="loading"><span>查找中... </span> </pre>
                <pre v-else><a href="javascript:void(0);" v-on:click="more()" >查看更多</a></pre>
            </div>
        </div>
    </div>

</template>

<script>
    import {api} from '../github';

    export default {
        name: "SearchPanel",
        props: {
            repo: String
        },
        data: () => {
            return {
                loading: false,
                query: {
                    q: null,
                    page: 1,
                    perPage: 10
                },
                searchItems: [],
                incompleteResults: false,
            };
        },
        mounted(){
            this.query.q = this.$route.params.keyword;
            this.query.page = 1;
            this.query.perPage = 10;
            this.search();
        },
        watch: {
            "$route.params.keyword" (value){
                this.query.q = value;
                this.query.page = 1;
                this.query.perPage = 10;
                this.searchItems = [];
                this.search();
            }
        },
        methods: {
            search(){
                this.loading = true;
                return api.repo(this.repo).search(this.query).then(promise => {
                    this.searchItems = this.searchItems.concat(promise.data.items);
                    this.incompleteResults = this.searchItems.length !== promise.data.total_count ;
                }).finally(() =>{
                    this.loading = false;
                })
            },
            more(){
                this.query.page ++;
                return this.search();
            },
            showFile(path){
                this.$router.push(api.routePath(this.repo, path));
            },
            pretty(fragment, matches){

                let result = "";
                let start = 0;
                let match = null;
                for (let i = 0; i < matches.length; i++) {
                    match = matches[i];
                    result += fragment.substring(start, match.indices[0]) + "<span class='pl-k'>" + match.text + "</span>";
                    start = match.indices[1];
                }
                return result + fragment.substring(start);
            }
        }
    }
</script>

<style scoped>
    @import '~github-markdown-css/github-markdown.css';

    .search-panel {
        flex: 1;
        margin: 1rem 0 2rem 0;
        padding: 1rem;
        overflow: hidden;
        background: #fff;
        box-sizing: border-box;
        box-shadow: 0 2px 10px rgba(0,0,0,.08);
    }

    pre {
        white-space: pre-wrap;       /* Since CSS 2.1 */
        white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
        white-space: -o-pre-wrap;    /* Opera 7 */
        word-wrap: break-word;       /* Internet Explorer 5.5+ */
    }

    .search-header pre.matchText {
        margin-left: 2rem;
    }
</style>
