import {Base64} from 'js-base64';
import Octokit from "@octokit/rest/index"
import { graphql } from "@octokit/graphql";
import preset from './github.config'


let config = {
    client: null,
    defaultClient: new Octokit({
        auth: "token " + preset.publicToken //public only
    }),

    graphqlClient: null,
    defaultGraphqlClient: graphql.defaults({
        headers: {
            authorization: `token ` + preset.publicToken
        }
    }),
    markdownPathReg: null,
    notePathReg: null
};

//initial
(() => {
    let markdownPathList = [];
    let notePathList = [];
    for (let key in preset.repos) {
        if (preset.repos.hasOwnProperty(key)) {
            let repo = preset.repos[key];
            if (repo.type === 'article') {
                markdownPathList.push(key);
            }
            if (repo.type === 'note') {
                notePathList.push(key);
            }

        }
    }
    config.markdownPathReg = markdownPathList.join("|");
    config.notePathReg = notePathList.join("|");
})();


function prepareGraphql() {
    if (!config.graphqlClient && config.token) {
        config.graphqlClient = graphql.defaults({
            headers: {
                authorization: `token ` + config.token
            }
        })
    }

    return new Promise((resolve, reject) => {
        if (config.graphqlClient) {
            resolve(config.graphqlClient);
        } else {
            resolve(config.defaultGraphqlClient);
        }
    });
}

function prepare() {
    if (!config.client && config.token) {
        config.client = new Octokit({
            auth: 'token ' + config.token
        });
    }

    return new Promise((resolve, reject) => {
        if (config.client) {
            resolve(config.client);
        } else {
            resolve(config.defaultClient);
        }
    });
}

function setContentType(promise){
    let contentType = promise.headers['content-type'] || "";

    if(contentType.indexOf('application/vnd.github.v3.raw') !== -1
        || contentType.indexOf('application/vnd.github.v3.html') !== -1){
        promise.type = "file"
    }

    if(contentType.indexOf("application/json") !== -1){
        if(Array.isArray(promise.data)){
            promise.type = "dir";
        }
        else{
            promise.type = promise.data && promise.data.type || null;
        }
    }
}

function getCookie(name) {
    let value = "; " + document.cookie;
    let parts = value.split("; " + name + "=");
    if (parts.length === 2) return parts.pop().split(";").shift();
}

let githubApi = {
    contents(path, format) {
        return prepare().then(client =>
            client.repos.getContents({
                "owner": this.owner,
                "repo": this.repo,
                "path": path,
                "headers": {
                    "If-None-Match": ""
                },
                "mediaType": {
                    "format": format || "html" // object raw
                }
            }).then(promise => {
                setContentType(promise);
                return promise;
            })

        );
    },
    remove(item) {
        return prepare().then(client => {
            return client.repos.deleteFile({
                "owner": this.owner,
                "repo": this.repo,
                "path": item.path,
                "message": "octokit: [master] page deleted",
                "sha": item.sha
            });
        });
    },
    commit(item) {
        return prepare().then(client => {
            return client.repos.createOrUpdateFile({
                "owner": this.owner,
                "repo": this.repo,
                "path": item.path,
                "message": "octokit: [master] page modified",
                "content": Base64.encode(item.text),
                "sha": item.sha
            });
        });
    },
    issues(label, after, size){
        return prepareGraphql().then(client => {
            return client({
                "query": `query listIssues($owner: String!, $repo: String!, $size: Int!, $after: String, $labels: [String!]) {
                          repository(name: $repo, owner: $owner) {
                            issues(first: $size, after: $after, labels: $labels, orderBy:{field: CREATED_AT, direction: DESC}){
                              edges {
                                node {
                                 comments(first: 2){
                                    totalCount
                                    pageInfo{
                                      hasNextPage
                                      hasPreviousPage
                                      startCursor
                                      endCursor
                                    }
                                    nodes {
                                      id
                                      body
                                      createdAt
                                      viewerCanDelete
                                      viewerCanUpdate
                                      viewerCanReact
                                      author{
                                        login
                                        avatarUrl
                                      }
                                    }
                                  }
                                  id
                                  number
                                  title
                                  body
                                  createdAt
                                  viewerCanUpdate
                                  viewerCanReact
                                  author {
                                    login
                                    avatarUrl
                                  }
                                }
                              }
                              pageInfo {
                                hasNextPage
                                hasPreviousPage
                                startCursor
                                endCursor
                              }
                              totalCount
                            }
                          }
                        }
                        `,
                "owner": this.owner,
                "repo": this.repo,
                "size": size || 10,
                "labels": [label],
                "after": after || null
            })
        });
    },
    createIssue(label, title, body){
        return prepare().then(client => {
            return client.issues.create({
                "owner": this.owner,
                "repo":this.repo,
                "labels": [label],
                "title": title,
                "body": body
            });
        });
    },
    deleteIssue(issueId){
        return prepareGraphql().then(client => {
            return client({
                "query": `mutation deleteIssue($clientMutationId: String!, $issueId: String!) {
                          deleteIssue(input:{clientMutationId:$clientMutationId,issueId:$issueId}) {
                            clientMutationId 
                            repository {
                              id
                            }
                          }
                        }`,
                "clientMutationId": Math.random().toString(36).slice(-5),
                "issueId": issueId
            });
        });
    },
    updateIssue(id, title, body) {
        return prepareGraphql().then(client => {
            return client({
                "query": `mutation updateIssue( $id: ID!, $title: String!, $body: String!) {
                              updateIssue(input:{ id: $id, title: $title, body: $body}) {
                                issue{
                                  id
                                }
                              }
                            }`,
                "id": id,
                "title": title,
                "body": body
            });
        })
    },
    issueComments(issueId, after, size) {
        return prepareGraphql().then(client => {
            return client({
                "query": `query queryIssueComment($issueId: ID!, $after: String, $size: Int!) {
                              node(id: $issueId) {
                                ... on Issue {
                                    comments(first: $size, after: $after){
                                      totalCount
                                      pageInfo{
                                        hasNextPage
                                        hasPreviousPage
                                        startCursor
                                        endCursor
                                      }
                                      nodes {
                                        id
                                        body
                                        createdAt
                                        viewerCanDelete
                                        viewerCanUpdate
                                        viewerCanReact
                                        author{
                                          login
                                          avatarUrl
                                        }
                                      }
                                    }
                                  }
                              }
                            }
                            `,
                "issueId": issueId,
                "after": after || null,
                "size": size || 10
            });
        });
    },
    createIssueComment(issueId, body){
        return prepareGraphql().then(client => {
            return client({
                "query": `mutation createIssueComment($clientMutationId: String!, $issueId: ID!, $body: String!) {
                              addComment(input: {clientMutationId: $clientMutationId, subjectId: $issueId, body: $body}) {
                                subject {
                                  id
                                }
                              }
                            }
                            `,
                "clientMutationId": Math.random().toString(36).slice(-5),
                "issueId": issueId,
                "body": body
            });
        });

    },
    deleteIssueComment(commentId){
        return prepareGraphql().then(client => {
            return client({
                "query": `mutation deleteIssueCommentInput($clientMutationId: String!, $commentId: ID!) {
                              deleteIssueComment(input: {clientMutationId: $clientMutationId, id: $commentId}) {
                                clientMutationId
                              }
                            }
                            `,
                "clientMutationId": Math.random().toString(36).slice(-5),
                "commentId": commentId
            });
        });
    },
    updateIssueComment(commentId, body){
        return prepareGraphql().then(client => {
            return client({
                "query": `mutation updateIssueComment($clientMutationId: String!, $commentId: ID!, $body: String!) {
                              updateIssueComment(input: {clientMutationId: $clientMutationId, id: $commentId, body: $body}) {
                                 issueComment{
                                    id
                                 }
                              }
                            }
                            `,
                "clientMutationId": Math.random().toString(36).slice(-5),
                "commentId": commentId,
                "body": body
            });
        });
    },
    user() {
        return prepare().then(client => {
            return client.users.getAuthenticated();
        })
    },
    search(query){
        return prepare().then(client => {
            return client.search.code({
                "q": query.q + "+repo:" + this.owner + "/" + this.repo,
                "page": query.page,
                "per_page" : query.perPage,
                "mediaType": {
                    'format': 'text-match+json'
                }
            })
        })
    },
    listNotes() {
        return prepareGraphql().then(client =>{
            return client({
                query: `query listNotes($owner: String!, $repo: String!) {
                          repository(name: $repo, owner: $owner) {
                            object(expression: "master:") {
                              ... on Tree {
                                entries {
                                  name
                                  object {
                                    ... on Blob {
                                      oid
                                      text
                                    }
                                  }
                                }
                              }
                            }
                          }
                        }`,
                owner: this.owner,
                repo: this.repo
            });
        });
    }
};

export const api = {
    repo(repoKey){
        let ctx = {};
        ctx.repo = preset.repos[repoKey].name;
        ctx.owner = preset.owner;
        return {
            ... ctx,
            ... githubApi
        }
    },
    parentDir(path) {
        return path.replace(/\/?([^/]+\/?)?$/, "");
    },
    defaultRepoKey(){
        return getCookie("repo") || preset.defaultRepoKey;
    },
    repos(){
        return preset.repos;
    },
    getRepo(repoKey){
        return preset.repos[repoKey];
    },
    routePath(repoKey, path){
        if(!repoKey) {
            repoKey = this.defaultRepoKey();
        }
        return "/" + preset.repos[repoKey].type + "/" + repoKey + ( path && ("/" + path) || preset.repos[repoKey].index);
    },
    authorized(token){
        config.token = token;
        config.client = null;
        config.graphqlClient = null;
    },
    markdownPathReg(){
        return config.markdownPathReg;
    },
    notePathReg() {
        return config.notePathReg;
    },
    preset(key){
        return preset[key];
    },
    ... githubApi,
};

