import Vue from 'vue'
import Vuex from 'vuex'
import {api} from './github'

Vue.use(Vuex);


const state = {
    token: null,
    user: {},
    repo: {},
    repoKey: null,
    file: {},//当前文件信息
    dir: "", //当前目录
    history: {}, //repo最新页面
    showDir: false //小屏展示目录树
};

//setter
const mutations = {
    receiveFile(state, payload) {
        state.file = payload;
    },
    receiveDir(state, payload) {
        state.dir = payload;
    },
    receiveUser(state, payload){
        state.user = payload;
    },
    receiveRepo(state, payload){
        state.repoKey = payload;
        state.repo = api.getRepo(payload);
    },
    receiveToken(state, payload){
        state.token = payload;
        api.authorized(payload);
    },
    receiveHistory(state, payload){
        state.history[payload[0]] = payload[1]
    },
    receiveMobile(state, payload){
        state.mobile = payload;
    },
    receiveToggleDir(state, payload){
        state.showDir = !state.showDir;
    }
};

const actions = {
};

export default new Vuex.Store({
    state,
    mutations,
    actions
})
