<template>

    <div class="header container">
        
        <div class="nav-item nav-cross">
            <div class="nav-menu-item" v-on:click="toggleDir()">
                <span class="nav-menu-label nav-icon iconfont icon-caidan"></span>
            </div>
           
        </div>
        
        <template v-if="$route.name !== 'editor'">
           <div class="nav-item nav-menu nav-front">
                <template v-for="(item, key) in repos">
                    <div :index="key" class="nav-menu-item" v-bind:class="key === repoKey ? 'curr': ''" v-on:click="switchRepo(key)" v-if="item.scope === 'public' || user.admin">
                        <span class="nav-menu-label">
                        {{item.name}} <span class="logo iconfont icon-jiesuo" v-if="item.scope === 'private'"></span>
                        </span>
                    </div>
                </template>
            </div>
        </template>

        <template v-if="$route.name === 'article' || $route.name === 'search' ">
            <div class="nav-item nav-search">
                <form role="search" class="nav-form" v-on:submit.prevent="search">
                    <input type="search" maxlength="32" placeholder="关键字" class="nav-input"
                           v-model="keyword"></input>
                    <a class="nav-icon iconfont icon-sousuo" v-on:click="search" ></a>
                </form>
            </div>
        </template>

        <div class="nav-item nav-control">
            <template v-if="$route.name === 'editor' && user.admin">
                <div class="nav-item">
                    <form role="search" class="nav-form" v-on:submit.prevent>
                        <input v-bind:readonly="file.sha" type="search" maxlength="32" placeholder="文件名称" class="nav-input"
                               v-model="filePath">.md
                        <a v-on:click="saveFile" class="nav-icon iconfont icon-baocun"> </a>
                        <a v-on:click="discardFile" class="nav-icon iconfont icon-chexiao"></a>
                        <a v-if="file.sha" v-on:click="removeFile" class="nav-icon iconfont icon-shanchu"></a>
                    </form>
                </div>
            </template>

            <div class="nav-item">
                <div class="nav-menu">
                    <div class="nav-menu-item" title="设为默认" v-if="defaultRepoKey() !== repoKey" v-on:click="resumeRepo(repoKey, true)">
                        <span class="nav-menu-label nav-icon iconfont icon-ding"></span>
                    </div>
                    <template v-if="$route.name === 'article' && user.admin">
                        <div class="nav-menu-item" title="新建文章" v-on:click="newFile">
                            <span class="nav-menu-label nav-icon iconfont icon-jiahao"></span>
                        </div>
                        <div class="nav-menu-item" v-if="file.sha" title="修改文章" v-on:click="editFile">
                            <span class="nav-menu-label nav-icon iconfont icon-bianjibiaoge"></span>
                        </div>
                    </template>
                    <div class="nav-menu-item" v-bind:title="user.login && ('@'+ user.login) || 'github登录'"
                         @mouseover="loginHover = true"
                         @mouseleave="loginHover = false" @click.exact="toggleLogin(repoKey && (repos[repoKey].scope === 'private'))" @click.ctrl.exact="goLogin(true)">
                        <span class="nav-menu-label">
                            <span v-if="user.login"  class="nav-icon iconfont " 
                               v-bind:class="{'icon-gerentouxiang': !loginHover && !user.admin, 'icon-guanliyuanrenzheng':  !loginHover && user.admin, 'icon-dengchu': loginHover}"></span>
                            <span v-else class="nav-icon iconfont icon-saomafuquan" ></span>
                        </span>
                    </div>
                </div>

            </div>
        </div>
    </div>


</template>

<script>
    import {api} from '../github';
    import {mapMutations, mapState} from 'vuex'


    export default {
        name: "Header",
        computed: {
            ...mapState([
                'file',
                'dir',
                'user',
                'token',
                'repoKey',
                "history",
                "showDir"
            ])
        },
        data() {
            return {
                processing: false,
                loginHover: false,
                keyword: null,
                filePath: null,
                repos: api.repos(),
                repo: null,
                path: null
            }
        },
        mounted() {
            this.initial();
        },
        watch: {
            "$route"(from, to) {
                this.initial();
            }
        },
        methods: {
            ...mapMutations([
                "receiveFile",
                "receiveDir",
                "receiveUser",
                "receiveRepo",
                "receiveToken",
                "receiveToggleDir"
            ]),
            initial() {
                this.repo = this.$route.params.repo;
                this.path = this.$route.params.path;

                if(this.$route.name === 'editor') {
                    //编辑文件状态
                    if (this.$route.params.sha) {
                        this.file.sha = this.$route.params.sha;
                        this.file.path = this.$route.params.path || '';
                        this.filePath = this.file.path.replace(/\.md$/, '');
                    } else{
                        //新建文件状态
                        this.filePath = this.dir && (this.dir + "/newfile") || "newfile";
                        this.receiveDir(this.$route.params.mkdir || "");
                    }
                }

                //搜索状态
                this.keyword = this.$route.params.keyword;

                //repo状态
                this.resumeRepo(this.$route.params.repo);

                //user状态
                this.resumeUser(this.$route.query.access_token, !!this.$route.query.admin);
            },
            switchRepo(key){
                console.info(this.history[key]);
                return this.$router.push(api.routePath(key, this.history[key]));
            },
            search() {
                return this.$router.push({
                    name: "search",
                    params: {
                        repo: this.repo,
                        keyword: this.keyword
                    }
                });
            },
            defaultRepoKey(){
                return api.defaultRepoKey();
            },
            resumeRepo(repoKey, markedDefault) {
                if(!repoKey){
                    let repoKey = this.$cookies.get("repo");

                    if(!repoKey){
                        repoKey = api.defaultRepoKey();
                        this.$cookies.set("repo", repoKey, Infinity);
                    }
                }

                this.receiveRepo(repoKey);

                if(markedDefault){
                    this.$cookies.set("repo", repoKey, Infinity);
                    this.$forceUpdate();
                }
            },
            resumeUser(token, admin) {
                let prevToken = this.$cookies.get("token");
                let user = this.$cookies.get("user") || {};

                if (token && prevToken !== token) {
                    this.$cookies.set("token", token, "7d");
                    this.receiveToken(token);

                    api.user().then(promise => {
                        user.login = promise.data.login;
                        user.avatar = promise.data.avatar_url + "&s=24";
                        user.admin = admin;
                        this.$cookies.set("user", JSON.stringify(user), "7d");
                        this.receiveUser(user);
                    })
                }else{
                    this.receiveToken(prevToken);
                    this.receiveUser(user);
                }

            },
            goHome(dirty) {
                if(dirty){
                    return this.$router.push({ path: api.routePath(this.repo, this.file.path || this.dir), query: {dirty: true}});
                }
                else{
                    return this.$router.push(api.routePath(this.repo, this.file.path || this.dir));
                }
            },
            toggleDir(){
                this.receiveToggleDir();
            },
            toggleLogin(me) {
                if(this.user.login){
                    this.logout();
                }
                else{
                    this.goLogin(me);
                }
            },
            goLogin(me) {
                return this.$router.push({
                    name: "login",
                    query: {
                        me: me,
                        redirectUrl: window.location.origin + window.location.pathname + "?admin=" + (me || '')
                    }
                });
            },
            logout() {
                this.$cookies.remove("token"); //清除sessionStorage
                this.$cookies.remove('user');//清除用户信息
                this.receiveUser({});
                this.receiveToken(null);
            },
            newFile() {
                this.receiveFile({}); //清除文件状态
                return this.$router.push("/add/" + this.repo + '/' + this.dir)
            },
            editFile() {
                return this.$router.push("/edit/"+  this.repo + '/' + this.file.sha + "/" + this.file.path)
            },
            removeFile() {
                if (this.processing) {
                    return;
                }
                let loading = this.$loading();
                this.processing = "remove";
                api.repo(this.repo).remove(this.file).then(promise => {
                     this.$success("删除成功");
                    return this.goHome(true);
                }).finally(() => {
                    this.processing = false;
                    loading.hide();
                });
            },
            saveFile() {
                if (this.processing) {
                    return;
                }
                let loading = this.$loading();
                this.processing = "save";
                if (!this.file.sha) {
                    this.file.path = this.filePath + '.md';
                }
                api.repo(this.repo).commit(this.file).then(promise => {
                    this.$success("保存成功");
                    return this.goHome(true);
                }).finally(() => {
                    this.processing = false;
                    loading.hide();
                });
            },
            discardFile() {
                this.goHome();
            }
        }
    }
</script>



<style scoped >

    .header {
        display: flex;
    }

    .nav-item {
        display: flex;
        align-items: center;
        flex-grow: 0;
        line-height: 4rem;
    }

    .nav-menu {
        display: flex;
    }

    .nav-menu-item {
        position: relative;
        cursor: pointer;
        padding: 0 1rem ;

    }

    .nav-front .nav-menu-item {
        font-size: 1.2rem;
        font-weight: bolder;
    }

    .nav-menu-item:hover {
        color: #1989fa;
    }

    .nav-menu-item:hover:after {
        transform: scale(1);
    }

    .nav-menu-item.curr {
        color: #1989fa;
    }

    .nav-menu-item.curr:after {
        transform: scale(1);
    }

    .nav-menu-item:after {
        content: "";
        display: inline-block;
        position: absolute;
        bottom: 0;
        left: .25rem;
        width:  calc(100% - .5rem);
        height: 2px;
        background: #1989fa;
        transform: scale(0);
        transition: all .3s;
    }

    .nav-menu-label {
        white-space: nowrap;
    }

    .nav-menu-label:after {
        position: absolute;
        content: '';
        margin-top: 1rem;
        margin-left: 1rem;
        width: 1px;
        height: 2rem;
        background-color: #ebebeb;
    }

    .nav-form {
        border: 1px solid #fff;
        background-color: rgba(227, 231, 236, .2);
        line-height: 2rem;
        margin-left: 1rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 2px;
    }

    .nav-form .nav-input {
        flex: 1 1 auto;
        justify-content: flex-end;
        border: none;
        width: 10rem;
        padding: .6rem 0 .6rem 1rem;
        box-shadow: none;
        outline: none;
        font-size: 1rem;
        background-color: transparent;

    }

    .nav-form .nav-icon {
        padding-left: .5rem;
    }

    .nav-form .nav-icon:last-child {
        padding-right: .5rem;
    }

    .nav-form .nav-icon:hover {
        cursor: pointer;
        color: #1989fa;
    }

    .nav-form:focus-within {
        border-color: #0366d6;
        background-color: #fff;
    }

    .nav-control {
        flex-grow: 1;
        justify-content: flex-end;
    }

    .header .iconfont {
        font-size: 1.4rem !important;
    }
    
    .header > .nav-cross {
        display: none;
    }
    
    @media all and (max-device-width: 800px){
        
        .header > .nav-front .nav-menu-item:not(.curr){
            display: none;
        }
        
        .header > .nav-search {
            display: none;
        }
        
        .header > .nav-cross {
            display: flex;
        }
        
       
    }

</style>
