<template>
    <div
            ref="editable"
            tabindex="-1"
            contenteditable
            @input="handleInput"
    ></div>
</template>

<script>
    export default {
        props: ["value"],
        mounted(){
            this.$refs.editable.innerText = this.value;
        },
        data(){
            return {
                internal: true
            }
        },
        methods: {
            handleInput(e) {
                this.internal = true;
                this.$emit("input", this.$refs.editable.innerText); //通知父组件
            }
        },
        watch: {
            value: {
                immediate: true,
                handler(val, oldVal) {
                    if(!this.internal){
                        //来自外部v-model的修改
                        this.$refs.editable.innerText = val;
                    }

                    this.internal = false;
                }
            }
        },
    };
</script>
