<template>
    <div class="article-tab">

        <template v-for="item in componentList">
            <keep-alive>
                <ArticlePanel v-bind:repo="item.repoKey" v-bind:path="path" v-if="repoKey === item.repoKey" :key="item.repoKey"> </ArticlePanel>
            </keep-alive>
        </template>

    </div>
</template>

<script>
    import {api} from "../github"
    import {mapState} from 'vuex'
    import ArticlePanel from './ArticlePanel'

    export default {
        components: {
            ArticlePanel
        },
        props: {
            "path": String,
            "repo": String
        },
        data(){
            return {
                componentList: this.articleList()
            }
        },
        computed: {
            ...mapState([
                'repoKey'
            ])
        },
        methods: {
            articleList(){
                let articleItems = [];
                let repos = api.repos();
                for(let key in repos){

                    if(repos.hasOwnProperty(key) && repos[key].type === 'article'){
                        let item = {};
                        item.name = repos[key].name;
                        item.repoKey = key;
                        articleItems.push(item);
                    }
                }

                return articleItems;
            }
        }
    };
</script>
