<template>

    <div class="container notebook-container">
        <div class="metro-box u-drag-content text-box" v-for="(note, index) in notes">
            <div class="metro-style-bg">
                <div class="outline">
                    <em class="triangle"></em>
                    <div v-show="index === editable" class="content-wrapper editable">
                        <EditableDiv :id="index" class="content" v-model="note.text"></EditableDiv>
                    </div>
                    <div v-show="index !== editable" class="content-wrapper">
                        <div class="content">{{note.text}}</div>
                    </div>
                </div>
                <div class="note-footer">
                    <span class="note-time"> {{moment(new Date(parseInt(note.path))).format('YYYY-MM-DD HH:mm:ss')}}</span>

                    <div class="note-icons">
                        <template v-if="index === editable">
                            <span class="iconfont icon-baocun" v-on:click="save(index, note)"></span>
                            <span class="iconfont icon-chexiao" v-on:click="undo(index)"></span>
                        </template>
                        <template v-else>
                            <span class="iconfont icon-bianjibiaoge" v-on:click="modify(index, note)"></span>
                            <span class="iconfont icon-shanchu" v-on:click="remove(index, note)"></span>
                        </template>
                    </div>

                </div>
            </div>
        </div>

        <div class="metro-box u-drag-content text-box">
            <div class="metro-style-bg">
                <div class="outline">
                    <em class="triangle"></em>
                    <div v-show="-1 === editable" class="content-wrapper editable">
                        <EditableDiv id="-1" class="content" v-model="newNote.text"></EditableDiv>
                    </div>
                    <div v-show="-1 !== editable" class="content-wrapper">
                        <span class="iconfont icon-jiahao icon-new" v-on:click="modify(-1)"></span>
                    </div>
                </div>
                <div class="note-footer" v-if="-1 === editable">
                    <div class="note-icons">
                        <span class="iconfont icon-baocun" v-on:click="save(-1, newNote)"></span>
                        <span class="iconfont icon-chexiao" v-on:click="undo(-1)"></span>
                    </div>
                </div>
            </div>
        </div>
    </div>


</template>

<script>

    import { api } from '../github';
    import EditableDiv from '../components/EditableDiv'


    export default {
        name: "NotePanel",
        props: {
            repo: String,
            path: String,
        },
        components: {
            EditableDiv
        },
        data() {
            return {
                editable: false,
                notes: [],
                newNote: {
                    path: null,
                    text: null
                },
                currNote: null
            }
        },
        mounted() {
            this.lists();
        },
        methods: {
            lists() {
                return api.repo(this.repo).listNotes().then(resp => {

                    if (resp.repository && resp.repository.object.entries) {
                        this.notes = [];
                        resp.repository.object.entries.forEach(note => {
                            let noteData = {};
                            noteData.sha = note.object.oid;
                            noteData.path = note.name;
                            noteData.text = note.object.text;
                            this.notes.push(noteData);
                        });
                    }
                });
            },
            undo(index) {
                if (index === -1) {
                    this.newNote = this.currNote;
                } else {
                    this.notes[index] = this.currNote;
                }
                this.editable = false;

            },
            save(index, item) {
                if (!item.sha) {
                    item.path = new Date().getTime();
                }
                let loading = this.$loading();
                api.repo(this.repo).commit(item).then((resp) => {
                    item.sha = resp.data.content.sha;
                    this.editable = false;

                    if (index === -1) {
                        this.undo(-1);
                        this.lists().then(resp => {
                            this.$success('保存成功');
                        }).finally(e => {
                        });
                    } else {
                        this.$success('编辑成功');
                    }
                }).catch(error => {
                    this.$fail(error);
                    this.undo(index);
                }).finally(() => {
                     loading.hide();
                }) 
            },
            modify(index, item) {
                if (this.editable !== false) {
                    this.undo(this.editable);
                }

                this.editable = index;
                this.currNote = item && JSON.parse(JSON.stringify(item)) || {path: null, text: null};
                this.$nextTick(() => {
                    document.getElementById(index).focus();
                })
            },
            remove(index, item) {
                this.$confirm('删除笔记' + item.path + ', 是否继续?', () => {
                    let loading = this.$loading();
                    api.repo(this.repo).remove(item).then(() => {
                        this.$success('删除成功');
                        this.lists().then(() => {
                        }).finally(e => {
                        });

                    }).catch(error => {
                        this.$fail(error);
                    }).finally(() => {
                        loading.hide();
                    })
                })

            }
        }
    }
</script>


