import Vue from 'vue'
import Router from 'vue-router'

import {api} from './github';

import Home from './views/Home.vue';
import Editor from './views/Editor.vue';
import SearchPanel from './views/SearchPanel.vue';
import ArticleTab from './views/ArticleTab.vue';
import NotePanel from './views/NotePanel.vue';
import DirectoryPanel from './views/DirectoryPanel.vue';
import Login from './views/Login.vue';

Vue.use(Router);


export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
        path: '/',
        redirect: api.routePath()
    },
    {
      path: '/',
      component: Home,
      children: [
        {
          name: 'note',
          path: '/note/:repo(' + api.notePathReg() + ')',
          component: NotePanel
        },
        {
          name: 'article',
          path: '/article/:repo(' + api.markdownPathReg() + ')/:path*',
          component: ArticleTab
        },
        {
          path: '/edit/:repo(' + api.markdownPathReg() + ')/:sha/:path*',
          component: Editor,
          name: 'editor'
        },
        {
          path: '/add/:repo(' + api.markdownPathReg() + ')/:mkdir*',
          component: Editor,
          name: 'editor'
        },
        {
          name: 'search',
          path: '/search/:repo(' + api.markdownPathReg() + ')/:keyword',
          component: SearchPanel
        },
        {
          name: 'login',
          path: '/login',
          component: Login
        }
      ]
    },
    /*{
      // 会匹配所有路径
      path: '*',
      redirect: api.routePath()
    }*/
  ]
})
